import React from 'react';
import ReactDOM from 'react-dom';

const BaselineRouter = require('./BaselineRouter.jsx');

module.exports = function ({atlasUrl = `https://www.ebi.ac.uk/gxa`, target = 'gxaBaselineTab', facetsTreeData, geneQuery, conditionQuery, species}) {
    ReactDOM.render(
        React.createElement(BaselineRouter, {atlasUrl, facetsTreeData, geneQuery, conditionQuery, species}),
        document.getElementById(target)
    );
};



